import useModals from '../components/hooks/useModals';
import { modals } from '../components/modal/Modals';
import { roomData } from '../components/Data';

function Rooms() {
	const { openModal } = useModals();
	const handleClick = () => {
		openModal(modals.Room, {
			onSubmit: () => {
				console.log('  ...');
			},
		});
	};
	return (
		<div className="content rooms">
			{roomData.map((room) => (
				<button className="detail">
					<h2 className="title">
						{room.en}
						<span className="h3"> {room.ko}</span>
					</h2>
					<div className="roomData-figure">
						<i className="img">
							<img src={'/img/room/' + room.img} alt="" />
							<img src={'/img/room/' + room.img} alt="" />
						</i>
						<p className="caption">{room.imgCaption}</p>
					</div>
					<div className="cont">
						<p>{room.personnel}</p>
						<p>{room.config}</p>
						<p>{room.size}</p>
					</div>
				</button>
			))}
			<div className="map">
				<h2 className="title"></h2>
				<h3 className="sub-title">     .</h3>
				<div className="map-figure">
					{/* <img src="/img/overview.jpeg" alt="" /> */}
					<img src="/img/overview.jpg" alt="" />
					<p className="caption">     </p>
				</div>
				<div className="info-list">
					<dl className="info">
						<dt>
							<h4>SKY 2   6</h4>
						</dt>
						<dd>
							<button>
								<i className="s1">S1</i> <span>SKY1</span>
							</button>
							<button>
								<i className="s2">S2</i> <span>SKY2</span>
							</button>
							<button>
								<i className="s3">S3</i> <span>SKY3</span>
							</button>
							<button>
								<i className="s4">S4</i> <span>SKY4</span>
							</button>
						</dd>
						<dt>
							<h4>CLOUD 2   3</h4>
						</dt>
						<dd>
							<button>
								<i className="c1">S1</i> <span>CLOUD1</span>
							</button>
							<button>
								<i className="c2">S2</i> <span>CLOUD2</span>
							</button>
							<button>
								<i className="c3">S3</i> <span>CLOUD3</span>
							</button>
							<button>
								<i className="c4">S4</i> <span>CLOUD4</span>
							</button>
						</dd>
						<dt>
							<h4>VALLY 4   8</h4>
						</dt>
						<dd>
							<button>
								<i className="v1">V1</i> <span>VALLY1</span>
							</button>
							<button>
								<i className="v2">V2</i> <span>VALLY2</span>
							</button>
						</dd>
						<dt>
							<h4>MOUNTAIN 2   6</h4>
						</dt>
						<dd>
							<button>
								<i className="m">M</i> <span>MOUNTAIN</span>
							</button>
						</dd>
						<dt>
							<h4>  </h4>
						</dt>
						<dd>
							<button>
								<i className="p1">P1</i> <span> 1 </span>
							</button>
							<button>
								<i className="p2">P1</i> <span> 1 </span>
							</button>
							<button>
								<i className="p3">P1</i> <span> 1 </span>
							</button>
							<button>
								<i className="o">O</i> <span></span>
							</button>
							<button>
								<i className="t">T</i> <span></span>
							</button>
						</dd>
					</dl>
				</div>
				<div className="btn-modal">
					<button className="s1" onClick={handleClick}>
						S1
					</button>
					<button className="s2" onClick={handleClick}>
						S2
					</button>
					<button className="s3" onClick={handleClick}>
						S3
					</button>
					<button className="s4" onClick={handleClick}>
						S4
					</button>
					<button className="c1" onClick={handleClick}>
						C1
					</button>
					<button className="c2" onClick={handleClick}>
						C2
					</button>
					<button className="c3" onClick={handleClick}>
						C3
					</button>
					<button className="c4" onClick={handleClick}>
						C4
					</button>
					<button className="v1" onClick={handleClick}>
						V1
					</button>
					<button className="v2" onClick={handleClick}>
						V2
					</button>
					<button className="m" onClick={handleClick}>
						M
					</button>
					<button className="p1" onClick={handleClick}>
						P1
					</button>
					<button className="p2" onClick={handleClick}>
						P2
					</button>
					<button className="p3" onClick={handleClick}>
						P3
					</button>
					<button className="o" onClick={handleClick}>
						O
					</button>
					<button className="t" onClick={handleClick}>
						T
					</button>
				</div>
			</div>
		</div>
	);
}

export default Rooms;
