import ReactModal from 'react-modal';

const AccountInfo = ({ onSubmit, onClose }) => {
	// const handleClickSubmit = () => {
	// 	onSubmit();
	// };

	const handleClickCancel = () => {
		onClose();
	};

	return (
		<ReactModal isOpen className="Modal.defaultStyles">
			<div className="modal">
				<div className="modal-title">
					<h3></h3>
					<button className="modal-close" onClick={handleClickCancel}>
						<span className="hide"></span>
					</button>
				</div>
				<div className="modal-content">
					<table>
						<colgroup>
							<col width="30%" />
						</colgroup>
						<tbody>
							<tr>
								<th></th>
								<td></td>
							</tr>
							<tr>
								<th></th>
								<td>110035446630</td>
							</tr>
							<tr>
								<th></th>
								<td></td>
							</tr>
						</tbody>
					</table>
				</div>
				{/* <div className="modal-button-area">
					<button onClick={handleClickCancel}></button>
					<button onClick={handleClickSubmit} className="sub"></button>
				</div> */}
			</div>
		</ReactModal>
	);
};

export default AccountInfo;
